import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Grid, Row, Col } from 'react-bootstrap';
import Tile from './Tile';
import Header from './Header';
import DemoForm from './Form';
import Footer from './Footer';

let imgUrl = 'images/black_businessman_2.jpg'
let heroStyle = {
  // backgroundImage: 'url(' + imgUrl + ')',
  backgroundSize: 'cover',
  overflow: 'hidden',
  // height: '50em',
  textAlign: 'left'
};
let brand = {
  textAlign: 'center'
}

let spacing = {
  marginTop: "5em"
}

let graphic1 = 'images/graphic_2.jpg';
let graphic2 = 'images/graphic_gears.png';
let graphic3 = 'images/user_interface.png';
let graphic4 = 'images/graphic_4.jpg';
let graphic5 = 'images/graphic_5.jpg';

let tiles = 
[
  {
    title: 'Manage Paid Time Off', 
    description: 'Create PTO policies, approve time requests, and track accruals right in Pali.',
    src: 'images/tile_cal.svg'
  },
  {
    title: 'Get Support 24/7', 
    description: "Talk to a real person. Anytime. We're only a phone call, Slack message, live chat, or email away.",
    src: 'images/tile_clock.svg'
  },
  {
    title: 'Pay Hourly Employees', 
    description: "Just fill out simple timecards and we'll take care of the payroll.",
    src: 'images/tile_piggy.svg'
  },
  {
    title: "Enroll in Benefits Online", 
    description: "Employees can enroll in any or all the benefits and perks you choose to offer.",
    src: 'images/tile_heart.svg'
  },
  {
    title: 'Make One-Off Payments', 
    description: 'Make all the individual payments you need to vendors and contractors, at no extra cost.',
    src: 'images/tile_dollar.svg'
  },{
    title: 'Organize Important Docs', 
    description: "Add employee-specific paperwork and company policies to everyone's document centers.",
    src: 'images/tile_notebook.svg'
  }
]


class App extends Component {

  createTiles() {
    let tileArr = [];
    for (let tile of tiles) {
      tileArr.push(
        <Col xs={4}>
          <Tile title={tile.title} description={tile.description} src={tile.src}></Tile>
        </Col>
      )
    };

    return tileArr;
  }
        
  render() {
    return (
      <div className="App" style={heroStyle}>
        {/* Brand */}
        <Header></Header>
        
        {/* Side Margins */}
        <Grid>

          <div style={spacing} ></div>

          {/* Section 1 */}
          <Row className="section-1 section">
            <Col xs={6} xsOffset={0}>           
              <h1>Focus on your business. We'll focus on the rest.</h1>
              <h4>Payroll, benefits, HR, and compliance — all in one place. Justworks simplifies your business's back office complexities, so you can work fearlessly.</h4>
            </Col>
            <Col xs={6}><img className="graphic" src={graphic1} /></Col>
          </Row>

          {/* <div style={spacing} ></div> */}

          {/* Section 2 */}
          <Row className="section-2 section">
            <Col xs={6}><img  className="graphic" src={graphic2} /></Col>
            <Col xs={6} xsOffset={0}>
              <h1>An HR tool that does the thinking for you.</h1>
              <h4>The traditional approach to human resources just isn't designed for the way you work. But Pali is. 
                Let Pali focus on the gruntwork while you focus on your real job: running an organization, inspiring a team, practicing your passion and building a business.</h4>
            </Col>
          </Row>
        </Grid>

        {/* No Side Margins */}
        <Grid fluid={true} >

          {/* Section 3 */}
          <Row className="section-3 section">
            <Col xs={12} xsOffset={0} className="center">
                <h1>All in an easy-to-use system</h1>
            </Col>
            <Col xs={10} xsOffset={1}><img className="graphic" src={graphic3} /></Col>
          </Row>

          {/* Section 4 */}
          <Row className="section-4 section color-font-white color-background">
            <Col xs={4} xsOffset={2} className="right-border-dashed top-bottom-padding">
              <h2>One system, One price</h2>
              <h4>No contract required. Cancel anytime!</h4>
            </Col>
            <Col xs={4} xsOffset={0} className="center top-bottom-padding">
              <h2>$1000 <span className="smaller color-font-white">/ month</span></h2>
              <h4>+$100 / month per employee</h4>
            </Col>
          </Row>

          {/* Section 5 */}
          <Row className="section-5 section" id="features">
            <Col xs={10} xsOffset={1}>{ this.createTiles() }</Col>
          </Row>

          {/* Section 6 */}
          <Row className="section-6 section color-font-white color-background" id="demo">
            <Col xs={5} xsOffset={2}>
              <h2 className="demo-title bold">Ready to request a demo?</h2>
              <ol className="demo-list">
                <li><h4>We'll connect quickly to learn about your business.</h4></li>
                <li><h4>We'll walk you through the Justworks product.</h4></li>
                <li><h4>Together, we’ll build a program that works best for you.</h4></li>
                <li><h4>Get started using Pali!</h4></li>
              </ol>
            </Col>
            <Col xs={4} xsOffset={0} className="center"><DemoForm></DemoForm></Col>
          </Row>

          {/* Section 7 */}
          <Row className="section-7">
            <Col xs={12} xsOffset={0}><Footer></Footer></Col>
          </Row>

        </Grid>
      </div>
    );
  }
}

export default App;



