import React, { Component } from 'react';

class Tile extends Component {

    constructor(props) {
        super(props);
    }


    render() {
        return (
            <div className="tile">
                <img src={this.props.src} />
                <h4 className="tile-title">{this.props.title}</h4>
                <h4 className="tile-description">{this.props.description}</h4>
            </div>
        )
    }

}

export default Tile


